@import '../shared/typography.scss';
@import '../shared/variables.scss';
@import '../shared/mixins.scss';

.profile {
  padding: 8px;
  border-radius: 5px;

  &__username {
    font-weight: 700;
    margin: 0;
    margin-top: 8px;
  }

  &__email {
    margin: 0;
    margin-bottom: 16px;
    font-size: 14px;
  }

  &__avatar {
    border-radius: 50%;
    width: 120px;
    margin: 0 auto;
  }

  &__tuits {
    display: flex;
    flex-direction: column;

    &-title {
      font-size: 12px;
      margin: 0;
    }

    &-number {
      margin: 0;
    }
  }
}
